    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failEdit':
                this.onEditPhone();
                break;
        }
    }
    onSecondBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'successEdit':
                this.onPopupDiscard();
                break;
        }
    }
};
