    Watch('popupIsShown', { immediate: true })
], PageMain.prototype, "onPopupIsShownChange", null);
PageMain = __decorate([
    Component({
        components: {
            MessageBox,
            ButtonApp,
            ListSuppliers,
            PaginationApp,
            PopupSupplier,
            SearchApp
        },
        mixins: [
            MsgBoxTools
        ],
        computed: {
            ...mapState('system', [
                'breakpoint'
            ])
        }
    })
], PageMain);
export default PageMain;
