import { __decorate } from "tslib";
import { Vue, Component, Mixins, Watch } from 'vue-property-decorator';
import { bannersMapper } from '../module/store';
import sleep from '@/mixins/sleep';
import ButtonApp from '@/components/ButtonApp.vue';
import MessageBox from '@/components/MessageBox.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '../mixins/MsgBoxTools';
import ListBanners from '../components/ListBanners.vue';
import ToggleAmount from '../components/ToggleAmount.vue';
import PopupAmount from '../components/PopupAmount.vue';
import animateIfVisible from '@/mixins/animateIfVisible';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['list', 'activeAmount']),
        ...bannersMapper.mapGetters(['isLoading'])
    },
    methods: {
        ...bannersMapper.mapActions(['deleteBanner', 'updateActiveAmount'])
    }
});
let PageMain = class PageMain extends Mixins(MsgBoxTools, MsgBoxToolsApp, Mappers) {
    constructor() {
        super(...arguments);
        this.deleteId = null;
        this.secondBtn = null;
        this.popupAmountIsShown = false;
        this.amountForUpdate = null;
    }
    get failedFetchList() { return this.requestStatus === 'failFetchList'; }
    get activeAmountValue() { return this.activeAmount.value; }
    async onListChange() {
        await this.$nextTick();
        await sleep(500);
        animateIfVisible();
    }
    async onActiveAmountChange() {
        await this.$nextTick();
        animateIfVisible();
    }
    async onIsLoadingChange() {
        await this.$nextTick();
        animateIfVisible();
    }
    async mounted() {
        await this.$nextTick();
        animateIfVisible();
    }
    // CLICK HANDLERS
    onCreateClick() { this.$router.push({ path: '/banners/create' }); }
    onDeleteClick(id) {
        // store id here in case of repeated request
        if (id)
            this.deleteId = id;
        this.secondBtn = { type: 'danger', isPlain: true };
        this.requestStatus = 'beforeDelete';
        this.openMsgBox();
    }
    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'beforeDelete':
                this.deleteItem();
                break;
            case 'failSetAmount':
                this.updateAmount(this.amountForUpdate);
        }
    }
    onSecondBtnClicked() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failSetAmount':
                this.closePopupAmount();
        }
    }
    async openPopupAmount() {
        this.popupAmountIsShown = true;
        document.body.classList.add('modal-open');
    }
    closePopupAmount() {
        this.popupAmountIsShown = false;
        document.body.classList.remove('modal-open');
    }
    // STORE ACTIONS CALL
    deleteItem() {
        this.deleteBanner(this.deleteId)
            .then(async () => {
            this.secondBtn = { type: 'success', isPlain: true };
            this.requestStatus = 'successDelete';
            this.openMsgBox();
            this.deleteId = null;
            this.updateList();
            await sleep(3000);
            this.closeMsgBox();
        })
            .catch(() => {
            this.secondBtn = { type: 'danger', isPlain: true };
            this.requestStatus = 'failDelete';
            this.updateList();
            this.openMsgBox();
        });
    }
    updateList() {
        this.$emit('updateList');
    }
    updateAmount(amount) {
        if (amount === this.activeAmount.value) {
            this.closePopupAmount();
            this.closeMsgBox();
            return;
        }
        // stored amount here in case of repeated request
        this.amountForUpdate = amount;
        this.closePopupAmount();
        this.updateActiveAmount(amount)
            .then(() => {
            this.updateList();
        })
            .catch(() => {
            this.requestStatus = 'failSetAmount';
            this.secondBtn = { type: 'danger', isPlain: true };
            this.openMsgBox();
        });
    }
    async animateOneMoreTime() {
        await this.$nextTick();
        animateIfVisible();
    }
};
__decorate([
    Watch('list', { immediate: true })
], PageMain.prototype, "onListChange", null);
__decorate([
    Watch('activeAmountValue', { immediate: true })
], PageMain.prototype, "onActiveAmountChange", null);
__decorate([
    Watch('isLoading', { immediate: true })
], PageMain.prototype, "onIsLoadingChange", null);
PageMain = __decorate([
    Component({
        components: {
            ListBanners,
            MessageBox,
            ButtonApp,
            ToggleAmount,
            PopupAmount
        },
        mixins: [
            MsgBoxTools
        ]
    })
], PageMain);
export default PageMain;
