import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import vClickOuside from 'v-click-outside';
import ButtonApp from '@/components/ButtonApp.vue';
import MessageBox from '@/components/MessageBox.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import sleep from '@/mixins/sleep';
import { bannersMapper } from '../module/store';
import FormBanners from '../components/FormBanners.vue';
import PopupConflict from '../components/PopupConflict.vue';
import animateIfVisible from '../../../mixins/animateIfVisible';
import MsgBoxTools from '../mixins/MsgBoxTools';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['form', 'activeAmount', 'bannerCurrent', 'pageTypes']),
        ...bannersMapper.mapGetters(['listActive', 'formSort', 'formIsValid', 'formActiveFrom'])
    },
    methods: {
        ...bannersMapper.mapMutations(['setFormType', 'clearForm', 'setBannerCurrentSuccess', 'setValidationIsShown']),
        ...bannersMapper.mapActions(['createBanner', 'deactivateBanner', 'updateFormByBannerData'])
    }
});
let PageCreate = class PageCreate extends Mixins(MsgBoxTools, MsgBoxToolsApp, Mappers) {
    constructor() {
        super(...arguments);
        this.bannerId = null;
        this.secondBtn = null;
        this.popupFormIsShown = false;
    }
    get failedFetchList() { return this.requestStatus === 'failFetchList'; }
    get bannerConflict() { return this.listActive.find(b => b.position === this.formSort.value); }
    get bannerConflictId() { return this.bannerConflict && this.bannerConflict.id; }
    get isPageCreate() { return this.$route.path.includes('/banners/create'); }
    get isSmthToCommit() {
        try {
            // check if any field was changed
            const form = this.form.data;
            for (const field of form) {
                switch (field.name) {
                    case 'isActive':
                        break;
                    case 'file':
                        if (field.value)
                            return true;
                        break;
                    case 'pageType':
                        if (field.value !== this.pageTypes[0])
                            return true;
                        break;
                    case 'sort':
                        if (field.value.toString() !== this.activeAmount.value.toString())
                            return true;
                        break;
                    default:
                        if (!!field.value)
                            return true;
                        break;
                }
            }
        }
        catch { }
    }
    // HOOKS
    created() {
        this.setFormType('create');
        document.addEventListener('keydown', this.keydownHandler);
        if (this.bannerCurrent.data)
            this.updateFormByBannerData(this.bannerCurrent.data); // if free banner-cell was clicked on list, its position was stored
    }
    async mounted() {
        await this.$nextTick();
        animateIfVisible();
    }
    beforeDestroy() {
        document.removeEventListener('keydown', this.keydownHandler);
        this.clearForm();
        this.setBannerCurrentSuccess(null);
    }
    // METHODS POPUP CONFLICT
    openPopupConflict() {
        document.body.classList.add('modal-open');
        this.popupFormIsShown = true;
    }
    closePopupConflict() {
        document.body.classList.remove('modal-open');
        this.popupFormIsShown = false;
        // this.bannerConflictId = null
    }
    // CLICK HANDLERS
    onSubmit() {
        if (this.formActiveFrom.value && this.bannerConflictId) {
            if (!this.formIsValid)
                this.setValidationIsShown(true);
            else
                this.openPopupConflict();
        }
        else
            this.submitForm();
    }
    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'successCreate':
                this.goToPageEdit();
                break;
            case 'failDeactivate':
                this.deactivateBannerConflict();
                break;
            default:
                this.submitForm();
                break;
        }
    }
    onSecondBtnClick() {
        switch (this.requestStatus) {
            case ('successCreate'):
                this.goToPageMain();
                break;
            case 'failDeactivate':
                this.closeMsgBox();
                this.closePopupConflict();
                break;
            default:
                this.closeMsgBox();
                break;
        }
    }
    onConflictConfirm() {
        this.closePopupConflict();
        if (this.formActiveFrom.value)
            this.submitForm();
        else
            this.deactivateBannerConflict();
    }
    goToPageMain() { this.$router.push({ path: '/banners/list' }).catch(err => { }); }
    goToPageEdit() {
        if (!this.bannerCurrent.data)
            return;
        this.$router.push({ path: `/banners/edit/${this.bannerCurrent.data.id}` }).catch(err => { });
    }
    // OTHER HANDLERS
    keydownHandler(evt) {
        if (evt.key === 'Escape') {
            // if (!this.msgBoxIsShown && !this.popupFormIsShown) this.goToPageMain()
            if (this.popupFormIsShown)
                this.closePopupConflict();
            else if (this.msgBoxIsShown)
                this.closeMsgBox();
        }
    }
    onClickOutside(evt) {
        const targetIsPage = evt.srcElement.classList.contains('page-create');
        if (targetIsPage)
            this.goToPageMain();
    }
    // STORE ACTIONS CALL
    submitForm() {
        this.createBanner()
            .then(async () => {
            this.secondBtn = { type: 'success', isPlain: true };
            this.requestStatus = 'successCreate';
            this.openMsgBox();
            this.$emit('updateList');
            await sleep(3000);
            if (this.isPageCreate) {
                this.closeMsgBox();
                this.goToPageEdit();
            }
        })
            .catch(error => {
            if (this.formIsValid) {
                if (error.bannerId) {
                    this.openPopupConflict();
                }
                else {
                    this.secondBtn = { type: 'danger', isPlain: true };
                    this.requestStatus = 'failCreate';
                    this.openMsgBox();
                }
            }
        });
    }
    deactivateBannerConflict() {
        this.deactivateBanner(this.bannerConflictId)
            .then(() => {
            this.submitForm();
        })
            .catch(() => {
            this.requestStatus = 'failDeactivate';
            this.secondBtn = { type: 'danger', isPlain: true };
            this.openMsgBox();
        });
    }
};
PageCreate = __decorate([
    Component({
        directives: {
            clickOutside: vClickOuside.directive
        },
        components: {
            FormBanners,
            MessageBox,
            PopupConflict,
            ButtonApp
        }
    })
], PageCreate);
export default PageCreate;
