import { __decorate } from "tslib";
import { Vue, Component, Watch, Mixins } from 'vue-property-decorator';
import ButtonApp from '@/components/ButtonApp.vue';
import { bannersMapper } from '../module/store';
import vClickOutside from 'v-click-outside';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['activeAmount'])
    },
});
let PopupAmount = class PopupAmount extends Mixins(Mappers) {
    constructor() {
        super(...arguments);
        this.amount = null;
    }
    get activeAmountValue() { return this.activeAmount.value; }
    onActiveAmountValueChange(val) {
        this.amount = val;
    }
    // HOOKS
    created() {
        document.addEventListener('keydown', this.keyDownHandler);
    }
    async mounted() {
        this.amount = this.activeAmount.value;
    }
    beforeDestroy() {
        document.removeEventListener('keydown', this.keyDownHandler);
        this.amount = this.activeAmount.value;
    }
    // METHODS
    keyDownHandler(evt) {
        if (evt.key === 'Escape')
            this.cancelClicked();
    }
    confirmClicked() { this.$emit('confirm', this.amount); }
    cancelClicked() { this.$emit('cancel'); }
    onClickOutside(evt) {
        const targetIsModal = evt.srcElement.classList.contains('page-main__popup-amount');
        if (targetIsModal)
            this.cancelClicked();
    }
    onInputUpdate(val) {
        if (!Number(val))
            this.amount = null;
        else if (val.length > 1)
            this.amount = val.slice(0, 1);
    }
};
__decorate([
    Watch('activeAmountValue')
], PopupAmount.prototype, "onActiveAmountValueChange", null);
PopupAmount = __decorate([
    Component({
        directives: {
            clickOutside: vClickOutside.directive
        },
        components: {
            ButtonApp
        }
    })
], PopupAmount);
export default PopupAmount;
